                </van-button>
            </div>
        </div>

        <van-dialog v-model:show="user_setting" title=""
                    className="project_model_pop user_setting_pop"
                    overlay overlayClass="project_model_pop_overlay"
                    :capture="isCamera"
                    :showCancelButton=false
                    :showConfirmButton=false>
            <div class="project_dialog">
