                <div class="content nickNameContent" v-else-if="settingtype=='nickName'">
                    <van-field
                            v-model="nickName"
                            name="nickName"
                            clearable
                            clear-trigger="always"
                            label=""
                            @update:model-value	="onInput"
                            placeholder="输入昵称"
                            :rules="[{ required: true, message: '请输入昵称' }]"

                    />
                    <div class="nickNameHandleButton"   :class="{'active':nickNameStatus=='1'}">
