                            validate-trigger="onChange"
                            :rules="[{validator:validatorNewphone,message:'请输入正确手机号'}]"
                            placeholder="请输入新手机号"
                    />

                        <div class="input-wrap" >
                            <van-field v-for="(item,index) in codelist" :key="index"
                                       class="cell-phone"
                                       v-model="item.value"
                                       type="number"
                                       :disabled="!(validatorNewphoneStatue==true&&validatorPhoneStatue==true)"
                                       label=""
                                       :ref="'vcode'+index"
                                       @input="()=> {return onCodeInput(index)}"
                            />
                        </div>
                    </van-form>
                    <div class="getCode">
                        <div>
                            <span v-if="timeout==true">{{count}}后可重新发送</span>
<!--
                            <span v-else><svg-icon className="refreshicon" icon-class="refresh"></svg-icon>获取验证码 <span v-if="validatorNewphoneStatue==true&&validatorPhoneStatue==true">{{newphone}}</span></span>
-->
                            <span @click="getCodeHandle" :class="{'active':(validatorNewphoneStatue==true&&validatorPhoneStatue==true&&timeout==false)}" v-else><svg-icon className="refreshicon" icon-class="refresh"></svg-icon>获取验证码</span>
                        </div>
                        <!--<div class="getCodeButton" @click="getCodeHandle" :class="{'active':(validatorNewphoneStatue==true&&validatorPhoneStatue==true&&timeout==false)}">
                            <svg-icon className="arrow-right" icon-class="arrow-right" ></svg-icon>
                        </div>-->
                    </div>
                    <div class="phoneHandleButton"  :class="{'active':completeVCode==true}">
                        <svg-icon className="right" @click="phoneHandle"  icon-class="right"></svg-icon>
                    </div>
