<script>
    import { reactive, onMounted, toRefs,ref ,getCurrentInstance,watch} from 'vue'
    import { Toast } from 'vant'

    import sHeader from '@/components/SimpleHeader';

    import { useRouter } from 'vue-router'
    export default {
        components: {
            sHeader
        },
        setup() {
            const router = useRouter();
            const uploaderRef = ref();
            const internalInstance = getCurrentInstance();

            const state = reactive({
                user: {},
                fileList:[],
                settingtype:'',
                phone:'',
                newphone:'',
                isCamera:'',
                nickName:'',
                getCodeStatus:0,
                nickNameStatus:0,
                timeout:false,
                user_setting:false,
                completeVCode:false,
                validatorPhoneStatue:false,
                validatorNewphoneStatue:false,
                count: 5,
                codelist: [{
                    value: ""
                }, {
                    value: ""
                }, {
                    value: ""
                }, {
                    value: ""
                }],

            })
            watch(()=>state.codelist,(val)=>{
                var t = val.filter((e)=> !e.value);
                0 == t.length ? state.completeVCode = true : state.completeVCode = false
            },{deep:true})
            onMounted(async () => {
                state.user = {
                    nickName:'Michelle',
                    telephone:'189*****999',
                    uid:'43888888'
                }
            })
            const onInput = () => {
                if(state.nickName=='') state.nickNameStatus='0';
                else state.nickNameStatus='1';
            }
            const goBack = () => {
                router.go(-1)
            }
            const goLogin = () => {
                router.push({ path: `/login` })
            }
            const goRealAuthor = () => {
                router.push({ path: `/real-author` })
            }

            const goTo = (r, query) => {
                router.push({ path: r, query: query || {} })
            }
            // 返回 Promise
            const afterRead = (file) =>{
                console.log(file);
                state.user_setting=false;
           };
            const deleteFile = () =>{
               state.fileList=[];
            };
            const uploadFile = (type) =>{
                if(type=='camera') state.isCamera='camera';
                else state.isCamera=''
                uploaderRef.value?.chooseFile();
            };
           const userSetting= (type) =>{
               if(type=='nickName'){
                   state.nickName=state.user.nickName;
                   state.nickNameStatus='0'
               }else{
                   state.phone='';
                   state.newphone='';
                   state.codelist=[{value: ""}, {value: ""}, {value: ""}, {value: ""}]
                   state.completeVCode=false;
                       state.validatorPhoneStatue=false;
                       state.validatorNewphoneStatue=false;
               }
               state.settingtype=type;
               state.user_setting=true;

           };

            const nickNameHandle= () =>{
                if(state.nickNameStatus=='0'){
                    return;
                }
                state.user_setting=false;
                state.user.nickName=state.nickName;
                Toast.success('操作成功');
            };
            const phoneHandle= () =>{
                if(state.completeVCode==false){
                    return;
                }
                state.user_setting=false;
                state.user.telephone=state.newphone;
                Toast.success('操作成功');
            };


            const validatorNewphone = (val) =>{
                if(val==''){
                    state.completeVCode=false;
                    state.validatorNewphoneStatue=false;
                    return '请输入手机号';
                }
                let vali= /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(val);
                if(vali==false){
                    state.completeVCode=vali;
                }
                state.validatorNewphoneStatue=vali;
                return vali
               };
            const validatorPhone = (val) =>{
                if(val==''){
                    state.completeVCode=false;
                    state.validatorNewphoneStatue=false;
                    return '请输入手机号'
                }
                let vali= /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(val);
                state.validatorPhoneStatue=vali;
                if(vali==false){
                    state.completeVCode=vali;
                }
                return vali
            };
            const onSubmit = () =>{

            }

            const onCodeInput=(e)=>{
                if(state.codelist[e].value && state.codelist[e].value.length > 0&e!=3){
                    if(e < 3){
                        internalInstance.refs["vcode" + (e + 1)][0].focus();
                    }
                    if(state.codelist[e].value.length > 1){
                        var value = state.codelist[e].value;
                        for(var k=e;k<state.codelist.length;k++){
                            state.codelist[k].value = value[e-k];
                        }

                    }
                }else{
                    state.codelist[e].value = state.codelist[e].value.slice(0, 1)
                }

            }

            const   getCodeHandle=()=> {
                if(state.validatorNewphoneStatue==true&&state.validatorPhoneStatue==true&&state.timeout==false){
                    var  t = 5;
                    if(!state.timer||state.timer==null) {
                        state.count = t;
                        state.timeout = true;
                        state.timer = setInterval((() => {
                                if (state.count > 0 && state.count <= t) {
                                    state.count--
                                } else {
                                    state.timeout = false;
                                    clearInterval(state.timer);
                                    state.timer = null
                                }
                            }
                        ), 1000)
                    }
                }

            }
            return {
                ...toRefs(state),
                goBack,
                goTo,
                uploaderRef,
                afterRead,
                deleteFile,
                uploadFile,
                userSetting,
                onInput,
                nickNameHandle,
                validatorNewphone,
                validatorPhone,
                onCodeInput,
                onSubmit,
                getCodeHandle,
                phoneHandle,
                goLogin,
                goRealAuthor
            }
        }
    }
</script>
